// 함수를 만드는 mixin
@mixin 함수() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

.my-alert p {
  margin-bottom: 0;
}

// 만약 다른 색상의 alert UI 가 필요하다면?
// extend 사용가능
.my-alert2 {
  @extend .my-alert;
  background-color: #ffe591;
}
